import React from "react";
import { observer } from "mobx-react-lite";
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import CoffeeIcon from '@mui/icons-material/Coffee';
import { store } from "store";
import { TabPanelProps } from "./app.types";
import "./app.css";

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

function CustomTabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

export const App = observer(() => {
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  return (
    <div className="App">
      <Box sx={{ padding: 0, borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="">
          {store.source?.categories.map((category, index) => <Tab key={index} label={category.displayName} {...a11yProps(index)} />)}
        </Tabs>
      </Box>
      {store.source?.categories.map((category, index) => (
        <CustomTabPanel key={`${category.id}_${index}`} value={value} index={index}>
          <List sx={{ width: '100%', bgcolor: 'background.paper' }}>
            {category.products.map((product, index) => {
              const price = product.prices["0,2"] ?? product.prices["0,3"] ?? product.prices["0,4"] ?? product.prices["0,6"];
              return (
                <ListItem key={`${product.id}_${index}`} sx={{ ":hover": { cursor: "pointer" } }}>
                  <ListItemAvatar>
                    <Avatar>
                      <CoffeeIcon />
                    </Avatar>
                  </ListItemAvatar>
                  <ListItemText primary={product.displayName} secondary={` ${price}`} />
                </ListItem>
              )
            })}
          </List>
        </CustomTabPanel>
      ))}
    </div>
  );
});
