import { makeAutoObservable } from "mobx";
import { AppDomain } from "app-domain";

export class Store {
  public source: AppDomain.View | null = null;

  constructor() {
    makeAutoObservable(this);
  }

  public init = async () => {
    console.log("start");
    const source = await fetch("/data.json")
      .then((res) => res.json())
      .catch(() => {});

    console.log(source);

    this.source = source;
  };
}

export const store = new Store();
store.init();
